<template>
  <v-container class="mt-5">
    <v-data-table
      :headers="headers"
      :items="documents"
      sort-by="id"
      class="elevation-1 text-capitalize"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>List of Documents</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialogNewdocument" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" dark class="mb-2" v-on="on">New Document</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline primary--text">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-form ref="formNewDocument" v-model="validForm" lazy-validation>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.title"
                        label="Title"
                        required
                        :rules="titleRules"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.author" label="Author" :rules="titleRules"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.language"
                        label="Language"
                        :rules="titleRules"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-file-input
                        :rules="fileRules"
                        placeholder="Upload a File"
                        append-icon="mdi-file"
                        label="Document"
                        v-model="chosenFile"
                        accept=".txt"
                        required
                      ></v-file-input>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.view="{ item }">
        <v-tooltip right>
          <template v-slot:activator="{ on }">
            <v-icon
              class="mr-2"
              @click="viewItem(item)"
              color="primary"
              v-on="on"
            >mdi-book-open-page-variant</v-icon>
          </template>
          <span>View Document</span>
        </v-tooltip>
      </template>

      <template v-slot:item.translate="{ item }">
        <v-tooltip right>
          <template v-slot:activator="{ on }">
            <v-icon @click="translateItem(item)" color="primary" v-on="on">mdi-translate</v-icon>
          </template>
          <span>Translate</span>
        </v-tooltip>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { addFileIPFS } from "../util/ipfs";

export default {
  name: "Documents",

  data: () => ({
    validForm: true,
    dialogNewdocument: false,
    fileRules: [v => !!v || "File  is required"],
    titleRules: [v => !!v || "field is  required"], // for all text input fields
    chosenFile: null, // <- initialize the v-model prop
    data: null,
    docs: [],
    headers: [
      {
        text: "Document ID",
        align: "left",
        sortable: false,
        value: "id",
        class: "primary white--text subtitle-2"
      },
      {
        text: "Title",
        value: "title",
        sortable: false,
        class: "primary white--text subtitle-2"
      },
      {
        text: "Author",
        value: "author",
        sortable: false,
        class: "primary white--text subtitle-2"
      },
      {
        text: "Language",
        value: "language",
        sortable: false,
        class: "primary white--text subtitle-2"
      },

      {
        text: "View",
        value: "view",
        sortable: false,
        class: "primary white--text subtitle-2"
      },
      {
        text: "Translate",
        value: "translate",
        sortable: false,
        align: "center",
        class: "primary white--text subtitle-2"
      }
    ],

    editedIndex: -1,
    editedItem: {
      title: "",
      author: "",
      language: ""
    },
    defaultItem: {
      title: "",
      author: "",
      language: ""
    }
  }),

  computed: {
    F() {
      console.log(this.files);
      return this.files;
    },
    formTitle() {
      return this.editedIndex === -1 ? "New Document" : "New Document";
    },
    documents() {
      let doc = this.$store.getters.getDocuments;
      console.log(doc, "u");
      return doc;
    }
  },

  watch: {
    dialogNewdocument(val) {
      val || this.close();
    } /* ,
    files(n) {
      console.log(n, "nnnn");
    } */
  },

  async created() {
    /*   let file = await addFileIPFS("document.ipfs");
    console.log(file); */
  },
  async mounted() {
    await this.$store.dispatch("getDocumentsList");
  },

  methods: {
    async initialize() {
      await this.$store.dispatch("getDocumentsList");
    },

    viewItem(item) {
      this.$router.push("/viewdocument/" + item.id);
    },
    close() {
      this.dialogNewdocument = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    translateItem(item) {
      this.$router.push("/translatedocument/" + item.id);
    },
    async save() {
      try {
        let hash;
        if (this.$refs.formNewDocument.validate()) {
          if (!this.chosenFile) {
            return;
          }
          //let paaraghraphs=c.split('<&&>');
          const reader = new FileReader();

          reader.readAsText(this.chosenFile);
          reader.onload = async () => {
            //console.log(reader.result);
            hash = await addFileIPFS(reader.result);
            // console.log("is it working2", hash[0].hash);
            let content = reader.result.split("<&&>").filter(function(x) {
              return x.trim() != "";
            });
            let counter = content.length;
            // console.log(counter, content, "counter");
            let payload;
            let DocID;
            DocID = parseInt(new Date().getTime(), 10);
            payload = {
              DocID,
              DocLanguage: this.editedItem.language,
              IpfsHash: hash[0].hash,
              DocumentTitle: this.editedItem.title,
              Author: this.editedItem.author,
              counter: counter
            };
            if (!this.$store.state.contract) {
              await this.$store.dispatch("initWeb3");
              await this.$store.dispatch("initContract");

              await this.$store.dispatch("createDocument", payload);
              await this.$store.dispatch("getDocumentsList");

              this.close();
            } else {
              await this.$store.dispatch("createDocument", payload);
              //  console.log("nothing", payload);
              await this.$store.dispatch("getDocumentsList");

              this.close();
            }
          };
        } else {
          console.log("invalid");
        }
      } catch (e) {
        console.log(e.message);
      }
    }
  }

  /* ,





    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    } */
};
</script>
<style scoped>
.v-data-table-header__icon {
  color: "red" !important;
}
</style>
