const IPFS = require("ipfs-api");
const ipfs = new IPFS({
  host: "ipfs.infura.io",
  port: 5001,
  protocol: "https"
});
async function getFileIPFS(ipfsHash) {
  console.log("action", ipfsHash);

  let file = await ipfs.get(ipfsHash);
  // console.log("get file", file[0].content.toString("utf8"));
  return file[0].content.toString("utf8");
}
async function addFileIPFS(content) {
  const buffer = new Buffer(content);

  console.log(content);

  console.log(buffer);

  //let K=await ipfs.key.gen('my-key', {
  // type: 'rsa',
  //size: 2048
  //});

  // let flagsObj={onlyHash:false};//{onlyHash:true};

  let onlyHash = await ipfs.files.add(buffer);
  return onlyHash;
}
export { getFileIPFS, addFileIPFS };
