<template>
  <v-container>
    <v-row>
      <v-spacer></v-spacer>
      <h3>Translate Document</h3>
      <v-spacer></v-spacer>
    </v-row>
    <v-row>
      <v-col xs="6" md="6">
        <v-card>
          <v-card-title>Original Document</v-card-title>
          <v-card-subtitle>{{ document ? document.title : "" }}</v-card-subtitle>
          <v-card-text>
            <span class="mb-1">{{ document ? document.author : "" }}</span>
            <br />
            <span>{{ document ? document.language : "" }}</span>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col xs="6" md="6">
        <v-card>
          <v-card-title>Translation</v-card-title>
          <v-card-subtitle>{{ translationInfo ? translationInfo.title : "" }}</v-card-subtitle>
          <v-card-text>
            <span class="mb-1">{{ translationInfo ? translationInfo.translator : "" }}</span>
            <br />
            <span>{{ translationInfo ? translationInfo.language : "" }}</span>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col xs="6" md="6">
        <v-card class="mx-auto overflow-y-auto" max-width="568.5" height="360" max-height="360">
          <v-card-title>
            <v-icon left>mdi-text-subject</v-icon>
            <span>{{`paragraph: ${page}`}}</span>
            <v-spacer></v-spacer>

            <v-btn class="primary" small @click="dialog=true">
              <v-icon dark left>mdi-translate</v-icon>Translate
            </v-btn>
          </v-card-title>
          <v-card-text class="text--primary">
            <div>{{ content[page - 1] }}</div>
          </v-card-text>
        </v-card>
        <div></div>
      </v-col>
      <v-col xs="6" md="6">
        <v-card class="mx-auto overflow-y-auto" max-width="568.5" height="360" max-height="360">
          <v-card-title>
            <v-icon left>mdi-translate</v-icon>
            <span>{{`Translation: ${TranslationsPage}`}}</span>
            <v-spacer></v-spacer>
          </v-card-title>
          <v-card-text class="text--primary">
            <div>{{ translation ? translation : "" }}</div>
          </v-card-text>
        </v-card>
        <div>
          <v-pagination
            v-model="TranslationsPage"
            :length="translations.length - 1"
            :total-visible="8"
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
            class="mt-2"
          ></v-pagination>
        </div>
      </v-col>
    </v-row>

    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline primary--text">New Translation</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.title" label="Title"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.author" label="Author"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.language" label="Language"></v-text-field>
              </v-col>

              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.protein" label=" "></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { getFileIPFS } from "../../util/ipfs";
export default {
  name: "ViewDocument",

  data() {
    return {
      dialog: false,
      editedItem: {
        title: "",
        author: "",
        language: ""
      },
      DOCID: this.$route.params.docID,
      document: null,
      translations: [],
      page: 1,
      file: null,
      paragraphs: [],
      TranslationsPage: 1,
      translationInfo: null,
      translation: null
    };
  },
  computed: {
    content() {
      return this.file
        ? this.file.split("<&&>").filter(function(x) {
            return x != "";
          })
        : [];
    }
  },
  watch: {
    async page(newP, old) {
      console.log(old, newP);
      this.translation = null;
      this.translations = await this.$store.dispatch(
        "getParagrapheTranslatations",
        this.paragraphs[newP - 1]
      );
      await this.getTranslation(this.translations[0]);
    },
    async TranslationsPage(newP) {
      console.log(newP);
      await this.getTranslation(this.translations[newP - 1]);
    }
  },

  async created() {
    //console.log("state", this.$store.state.contract);
    /*     let result = [];
    let a = ["[1,2]", "[45,2,3]"];
    for (let i = 0; i < a.length; i++) {
      let arr = JSON.parse(a[i]);

      for (let j = 0; j < arr.length; j++) {
        result.push(arr[j]);
      }
    }
    console.log(result.join(",")); */
  },
  async mounted() {
    if (!this.$store.state.contract) {
      await this.$store.dispatch("initWeb3");
      await this.$store.dispatch("initContract");
      const document = await this.$store.dispatch("getDocument", this.DOCID);
      this.document = document;

      let file = await getFileIPFS(document.ipfs);
      this.file = file;
      this.paragraphs = await this.$store.dispatch(
        "getDocumentParagraphes",
        this.DOCID
      );
      this.translations = await this.$store.dispatch(
        "getParagrapheTranslatations",
        this.paragraphs[0]
      );
      await this.getTranslation(this.translations[0]);
    } else {
      const document = await this.$store.dispatch("getDocument", this.DOCID);
      this.document = document;

      let file = await getFileIPFS(document.ipfs);
      this.file = file;
      this.paragraphs = await this.$store.dispatch(
        "getDocumentParagraphes",
        this.DOCID
      );
      this.translations = await this.$store.dispatch(
        "getParagrapheTranslatations",
        this.paragraphs[0]
      );
      await this.getTranslation(this.translations[0]);
    }
  },

  methods: {
    async getTranslation(translationID) {
      this.translationInfo = await this.$store.dispatch(
        "getTranslation",
        translationID
      );
      this.translation = await getFileIPFS(this.translationInfo.ipfs);
    }
  }
};
</script>
